@import "~antd/dist/antd.css";
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  /* font-family: "Open Sans Condensed", sans-serif;
  font-family: "Roboto", sans-serif; */
}

.App {
  padding: 50px;
  font-family: "Lato", sans-serif;
}

.main-content-wrapper {
  max-width: 1200px;
  margin: auto;
}

a {
  color: #00adb5 !important;
  text-decoration: underline;
}

a:hover {
  color: #62bdbd !important;
}

.table {
  font-size: 10pt;
  caption-side: bottom;
  border-collapse: collapse;
  background: #282828 !important;
  color: #eeeeee !important;
}

.form-wrapper {
  text-align: center;
  /* font-family: "Roboto", sans-serif; */
  margin-top: 25px;
}

input[type="text"] {
  width: 100%;
  padding: 5px;
}

.submit-button {
  background-color: transparent;
  border: none;
  color: #eeeeee;
  padding: 5px 40px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 2px;
  border: 1px solid #00adb5;
  transition: 0.1s linear;
}

.submit-button:hover {
  background-color: #00adb5;
}

.spinner-wrapper {
  width: 100%;
  text-align: center;
}

.data-num {
  font-weight: bold;
}

.error-box {
  border: 1px solid #d44950;
  padding: 15px;
  background: #f8d7da;
  color: #d44950;
  margin-bottom: 0;
}

.alerts {
  margin-top: 10px;
  text-align: center;
}

.poster-img {
  width: 90px;
}

.search-amount {
  color: #eeeeee;
}

