/* ant */
.ant-tabs-ink-bar {
  position: absolute;
  background: #00adb5;
  pointer-events: none;
}

.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #00adb5;
  text-shadow: 0 0 0.25px currentColor;
}

.ant-tabs-tab-btn:focus,
.ant-tabs-tab-remove:focus,
.ant-tabs-tab-btn:active,
.ant-tabs-tab-remove:active {
  color: #00adb5;
}

.ant-tabs-tab-btn {
  outline: none;
  transition: all 0.3s;
  color: #eeeeee;
}

.ant-tabs-tab-btn:hover {
  outline: none;
  transition: all 0.3s;
  color: #00adb5;
}

/* DROPDOWN */
